.dropdown {
    position: relative;
    display: inline-block;
  }

  .dropdown-content {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 230px;

    padding: 10px;
    z-index: 1;
    .dropdown-item {
        padding: 10px;
        color: #888888;
        font-weight: normal;
      }
  }
  
  .dropdown:hover .dropdown-content {
    display: block;
  }
