header {
    position: relative;
    display: block;
    .header-top {
        position: absolute;
        left: 0;
        right: 0;
        background: transparent;
        padding: 32px 0;

        .desktop_logo {
            max-width: 165px;
            margin-right: 30px;
            margin-left: 30px;
            }

        .row {
            display: flex;
            align-items: center;

            .header-bar {
                flex: 1;
                margin-right: 30px;
                display: flex;
                justify-content: space-between;

                .navbar-list {
                    flex:9;
                    display: flex;

                    .navbar-item {
                        align-self: center;
                        padding: 10px 20px;
                        font-size: 14px;
                        font-weight: 700;
                        color: #253237;
                        font-family: "Open Sans",sans-serif;

                        &:hover{
                            text-decoration: underline;
                            cursor: pointer;
                        }
                    }
                }

                .cart-section{
                    flex:1;
                    padding: 10px 20px;
                    display: flex;

                    .cart-icon{
                        align-self: center;
                        flex:1;
                        padding: 10px 20px;
                        margin-top: 10px;
                        display:flex;

                        .search-icon{
                            flex:1;
                            padding: 5px 10px 15px;
                            &:hover{
                                opacity: 0.5;
                                cursor: pointer;
                                color:#4fb78d;
                            }
                        }
                        .bag-icon{
                            flex:1;
                            padding: 5px 10px 15px;
                            &:hover{
                                opacity: 0.5;
                                cursor: pointer;
                                color:#4fb78d;
                            }
                        }
                    }
                    .price-count{
                        align-self: center;
                        flex:1;
                        display: flex;
                        font-size: 14px;
                        font-weight: 700;
                        color: #253237;
                        font-family: "Open Sans",sans-serif;
                        &:hover{
                            opacity: 0.5;
                            cursor: pointer;
                            color:#4fb78d;
                        }
                    }
                }
            }
        }
    }

    .description {
        margin-top: 100px;
        margin: 100px 40px;
        font-family: "Open Sans",sans-serif;

        .title1 {
            font-size: 14px;
            font-style: italic;
            margin-bottom: 10px;
        }

        .title2 {
            font-size: 36px;
            font-weight: bold;
        }

        .title3 {
            font-size: 36px;
            font-weight: bold;
        }
    }
}
