.btn-shopnow {
    margin-top: 30px;
    padding: 30px 0px;
    text-decoration: none;
    background-color: #4FB68D; /* Green */
    border: none;
    color: white;
    padding: 10px;
    text-align: center;
    display: inline-block;
    font-family: "Open Sans",sans-serif;
    font-size: 10px;
    font-weight: bold;
    cursor: pointer;
    border-radius: 30%;
    &:hover{
        background-color: #000000;
    }
    .link-shopnow{
        text-decoration: none;
    }
}

